* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#root {
  height: 100%;
  margin: 0;
}

.grid-container {
  height: 100%;
  margin: 0;
  display: grid;
  grid-template-areas:
    'menu main right';
  grid-gap: 0%;
  background-color: lightsteelblue;
  padding: 0%;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 30%;
}

.App-header {
  background-color: #222;
  height: 200px;
  margin: 0%;
  color: white;
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.mainframe {
  grid-area: main;
  width: 50%;
  margin: auto;
}

.sideframe {
  grid-area: menu;
}

.newsframe {
  grid-area: right;
  max-width: 30%;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




